


















import { defineComponent, watch } from '@vue/composition-api'

// Composables
import { useRouter } from '@/use/router'
import { useTraceSearch } from '@/use/trace-search'

export default defineComponent({
  name: 'TraceFind',

  setup() {
    const { router, route } = useRouter()
    const traceSearch = useTraceSearch()

    watch(
      () => traceSearch.trace,
      (trace) => {
        if (trace) {
          router.replace({
            name: 'TraceShow',
            params: {
              projectId: String(trace.projectId),
              traceId: trace.id,
            },
          })
        }
      },
    )

    watch(
      () => route.value.params.traceId,
      (traceId) => {
        traceSearch.find(traceId)
      },
      { immediate: true },
    )

    function reload() {
      traceSearch.find(route.value.params.traceId)
    }

    return { traceSearch, reload }
  },
})
